<template><section><h2 id="radio-dan-xuan-an-niu"><a class="header-anchor" href="#radio-dan-xuan-an-niu" aria-hidden="true">¶</a> Radio 单选按钮</h2>


<h3 id="ji-ben-yong-fa"><a class="header-anchor" href="#ji-ben-yong-fa" aria-hidden="true">¶</a> 基本用法</h3>
<p>在 <code v-pre>el-radio</code> 根组件上加上类 <code v-pre>yuki-radio</code> 即可.</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;div&gt;\n    &lt;el-radio class=\&quot;yuki-radio\&quot; v-model=\&quot;radio\&quot; label=\&quot;1\&quot;&gt;备选项&lt;/el-radio&gt;\n    &lt;el-radio class=\&quot;yuki-radio\&quot; v-model=\&quot;radio\&quot; label=\&quot;2\&quot;&gt;备选项&lt;/el-radio&gt;\n    &lt;el-radio class=\&quot;yuki-radio\&quot; v-model=\&quot;radio\&quot; label=\&quot;3\&quot; :disabled=\&quot;true\&quot;&gt;备选项&lt;/el-radio&gt;\n&lt;/div&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            radio: '1'\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><div>
    <el-radio class="yuki-radio" v-model="radio" label="1">备选项</el-radio>
    <el-radio class="yuki-radio" v-model="radio" label="2">备选项</el-radio>
    <el-radio class="yuki-radio" v-model="radio" label="3" :disabled="true">备选项</el-radio>
</div>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-radio</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-radio"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">el-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-radio</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-radio"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">el-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-radio</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-radio"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"true"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">el-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="radio-attributes"><a class="header-anchor" href="#radio-attributes" aria-hidden="true">¶</a> Radio Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio的value</td>
<td>string/number/boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>border</td>
<td>是否显示边框</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>Radio的尺寸,仅在border为真时有效</td>
<td>string</td>
<td>medium/smal/mini</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生name属性</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="radio-events"><a class="header-anchor" href="#radio-events" aria-hidden="true">¶</a> Radio Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>绑定值变化时触发的事件</td>
<td>选中的Radio label值</td>
</tr>
</tbody>
</table>
<h3 id="radio-group-attributes"><a class="header-anchor" href="#radio-group-attributes" aria-hidden="true">¶</a> Radio-group Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>单选框组尺寸,仅对按钮形式的Radio或带有边框的Radio有效</td>
<td>string</td>
<td>medium/small/mini</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>name</td>
<td>原生name属性</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.demo-radio {
    .source {
        position: relative
    }
}
.demo-box.demo-alert .el-alert{
    margin: 20px 0 0;
}
.demo-box.demo-alert.el-alert:first-child {
    margin: 0;
}
</style>
<script>
export default{
    data() {
        return {
            radio: '1'
        }
    }
}
</script>


