ata)){\n                            newData.push(res)\n                        }\n                    })\n                    this.data.checkedList = [...newData]\n                }\n            }\n        },\n        include(val,data) {\n            let count = 0;\n            data.forEach(res=&gt;{\n                if(val.id === res.id){\n                    count++\n                }\n            })\n            if(count&gt;0){\n                return false\n            }\n            return true\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
